<template>
  <div class="supportBank bgfff">
    <div class="head">
        <span class="fs16 icon back-btn" @click="history">&#xe603; &nbsp;返回</span>
        <span class="bank-name fs14 fr pr12">支持的银行卡列表</span>
    </div>
    <ul class="bank-list" id="supportBank" >
      <li class="bank pl10 fs14 " v-for="item of supportBanklists" :key="item.bankName">
        <span class="logo dis-inline"><img  :src='this.contextPath+"/static/images/bank-logo/"+item.bankCode+".png"' width="20" height="20" /></span>
        <span class="bank-name dis-inline  t-ell">{{item.bankName}}</span>
        <span class="card-type dis-inline c999 " :class='{"c333":item.cardType=="DEBIT" || item.cardType=="CREDITDEBIT"}'>储蓄卡</span>
        <span class="card-type dis-inline c999 " :class='{"c333":item.cardType=="CREDIT" || item.cardType=="CREDITDEBIT"}' >信用卡 </span>  
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'supportBank',
  data () {
    return {
      supportBanklists:[],
       contextPath:contextPath//图片路径
    }
  },
  created() {
    var self = this;
    this.hideTip() //错误弹框提示
    this.$http.get(Request.url+'/wap/ajax/bankList',{params: {'token':token}}).then((res) => {
      var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body 
      if(data.bizStatus === 'success'){
        var supportBanklist
        if(!data.creditBankList){
          supportBanklist = data.debitBankList
        }
         if(!data.debitBankList){
          supportBanklist = data.creditBankList
        }
        if(data.debitBankList && data.creditBankList){
          supportBanklist = data.creditBankList.concat(data.debitBankList)
        }
        var  tempList = []    
        supportBanklist.forEach( item => {
          var  existCard = tempList.find( arrItem => arrItem.bankCode == item.bankCode)
          if (existCard) { // 如果之前已经添加过card，则表示此card支持两种类型,只修改,不push
            existCard.cardType = 'CREDITDEBIT'
            return
          }
          tempList.push({ bankCode: item.bankCode, bankName: item.bankName, cardType: item.cardType }) // 如果之前不存在,则直接添加到列表
        })   
        self.supportBanklists = tempList
      }
    })
  },
  methods:{
    history(){
      this.$router.go(-1)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  lang="less">
 .hidden {
    visibility: hidden;
  }
.head {
  .back-btn{
    display: inline-block;
    height:100%;
  }
  padding-left: 10px;
  height: 50px;
  background-color: #fff;
  color: #333;
  line-height: 50px;
  font-size: 14px;
}
.bank-list{
  .bank{
    border-top:1px solid #ddd;
    height: 40px;
    .logo{
      vertical-align: top;
      line-height: 40px;
    }
    .bank-name{
      width:35%;
      line-height: 40px;
    }
    .card-type{
       width:25%;
      vertical-align: top;
      line-height: 40px;
    }
  }
}

</style>



