import Vue from 'vue'
import Router from 'vue-router'
import Resource from 'vue-resource'
import vueEventBus from 'vue-event-bus'
import EnterCardNo from '@/views/enterCardNo'   //输入银行卡号  无绑卡关系
import SupportBank from '@/views/supportBank'   //限额列表
import AddBankCard from '@/views/addBankCard'  //添加银行卡组件
import BankCardList from '@/views/bankCardList'  //卡列表  有绑卡关系
import AddItem from '@/views/addItem'  //补充项
import Home from '@/views/home'  //默认页  功能：新老绑卡关系选择
Vue.use(Router)
Vue.use(Resource)
Vue.use(vueEventBus)
const router = new Router({
  routes: [  
    {
      path: '/',
      name: 'Home',   //输入银行卡号  无绑卡关系 和已有绑卡关系的父级
      component: Home
    }, 
    {
      path: '/SupportBank',   //银行卡限额列表
      name: 'SupportBank',
      component: SupportBank
    },
    {
      path: '/EnterCardNo',   //输入银行卡号
      name: 'EnterCardNo',
      component: EnterCardNo
    },
    {
      path: '/AddBankCard/:cardNo/:token',  //添加银行卡组件
      name: 'AddBankCard',
      component: AddBankCard
    },
    {
      path: '/AddItem/:token/:bindId',  //补充项
      name: 'AddItem',
      component: AddItem
    }
  ]
})
export default router;
 




