<template>
  <div class="addBankCard">
    <div class="wrapper b-transparent">
      <!-- 头部 start -->
      <headerOrderInfo></headerOrderInfo>
      <!-- 头部 end -->
      <!-- 商户基本信息 -->
      <p class="c999 fs12 pl12 pt10">请填写银行卡信息</p>
      <!-- 绑卡支付和单独绑卡共有信息 -->
      <div class="content mt10">
        <!-- 银行卡部分 -->
        <div class="bankCard bgfff icon pl12 bt bb pr">   <!-- 点击显示银行弹框 -->
          <div class="dis-inline ver-t mt4">
            <img  :src='this.contextPath+"/static/images/bank-logo/"+bankCardInfo.bankCode+".png"' width="16" height="16" alt="banklogo">
            <!-- <img :src='"../../static/pre-authorization-static/assets/bank-logo/"+bankCardInfo.bankCode+".png"' width="18" height="18" alt="banklogo"> -->
          </div>
          <div class="dis-inline">
            <p class="fs16 c666 mb10">{{bankCardInfo.bankName}}</p>
            <i class="fs16 c666 mr10">尾号{{bankCardInfo.cardLater4}}</i>
            <i class="fs14 c999 mt10" v-if="bankCardInfo.cardType == 'DEBIT'">储蓄卡</i>
            <i class="fs14 c999 mt10" v-if="bankCardInfo.cardType == 'CREDIT'">信用卡</i>
          </div>
          <router-link tag="span" class="change" :to="{name: 'EnterCardNo'}">更换</router-link>
        </div>
        <!-- 商户基本信息 -->
        <p class="c999 fs12 pl12 pt10 pb10 bold">请使用<i class="cff791f">本人信息</i>进行预授权，预授权成功后不可更换</p>
        <form class="bt bgfff fs18" name="form" id="form">
          <label v-if="cardItemNecessary.needOwner">
            <div class="info li pr " :class='{"bgEBEBE4":cardInfo.owner}'>
              <mark class="dis-inline text c333 pl12">姓名</mark>
              <input v-if="cardInfo.owner" class=" input-text" name="owner" _name="chinese" encrypt="true" type="text" :value="cardInfo.owner" :disabled="true">
              <input v-else class="required input-text" name="owner" _name="chinese" encrypt="true" type="text" placeholder="您的真实姓名" @blur.prevent="formatCheck($event)">
              <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
            </div>
          </label>          
          <label v-if="cardItemNecessary.needIdNo">
            <div class="info bt li pr error" :class='{"bgEBEBE4":cardInfo.idNo}'>
                <mark class="dis-inline text c333 pl12">身份证号码</mark>
                <input v-if="cardInfo.idNo" class=" input-text" name="idNo" encrypt="true" type="tel" :value="cardInfo.idNo" :disabled="true">
                <input v-else class="required input-text" name="idNo" _name="idno" maxlength="18" encrypt="true" type="tel" placeholder="真实证件号" @blur.prevent="formatCheck($event)">
                <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
            </div>
          </label>
          <div>
            <label v-if="cardItemNecessary.needCvv">
              <div class="info li bt pr" :class='{"bgEBEBE4":cardInfo.cvv}'>
                <mark class="dis-inline text c333 pl12">C V V</mark>
                <input v-if="cardInfo.cvv" class=" input-text" name="cvv"  encrypt="true" type="tel" :value="cardInfo.cvv" :disabled="true">
                <input v-else class="required input-text" name="cvv" _name="threeOrFour" maxlength="4" encrypt="true" type="tel" placeholder="卡背面后三位数字" @blur.prevent="formatCheck($event)">
                  <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
              </div>
            </label>
            <label v-if="cardItemNecessary.needAvlidDate">
              <div class="info bt li pr" :class='{"bgEBEBE4":cardInfo.avlidDate}'>
                  <mark class="dis-inline text c333 pl12">有效期</mark>
                  <input v-if="cardInfo.avlidDate" class=" input-text" name="avlidDate"  encrypt="true" type="tel" :value="cardInfo.avlidDate" :disabled="true">
                  <input v-else class="required input-text" name="avlidDate" _name="fourNo" maxlength="4" encrypt="true" type="tel" placeholder="月/年(如09/15 输入0915)" @blur.prevent="formatCheck($event)">
                  <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
              </div>
            </label>
          </div>
          <label v-if="cardItemNecessary.needPhoneNo" >
            <div class="info bt li pr error" :class='{"bgEBEBE4":cardInfo.phoneNo}'>
              <mark class="dis-inline text c333 pl12">手机号</mark>
              <input v-if="cardInfo.phoneNo" class=" input-text" name="phoneNo" encrypt="true" type="tel" :value="cardInfo.phoneNo" :disabled="true">
              <input v-else class="required input-text" name="phoneNo" _name="phone" maxlength="11" encrypt="true" type="tel" placeholder="银行预留手机号" @blur.prevent="formatCheck($event)">
              <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
            </div>
          </label>
           
        </form>
        <div class="pt10 pb10 pl12">
          <p class="fs16 c999">
          <label for="agreement">
            <input class="hide" id="agreement" type="checkbox" :checked="check" v-model="check">
            <i class="icon agreement"></i>已阅读并同意
          </label><a class="c2eb944 pr protocal" @click="showProtocal">《服务协议》</a></p>
           
        </div>
        <div class="pl12 pr12 mt10">
          <button class="w" :disabled="!check" id="submit" type="button" @click="sureAuthorization">
            <i>确认预授权</i>
          </button>
        </div>
      </div>
      <!-- 验证码 开始-->
      <shortMessage :isShowVerify="isShowVerify"></shortMessage>
      <!-- 验证码 结束-->
      <!-- 协议 -->
        <Protocal :isProtocal="isProtocal"></Protocal>
      <!-- 协议 -->
      <div class="bg-fixed" v-show="isShowMask" id="mask" @click="mask"></div>
    </div>
    
  </div>
</template>

<script>
import headerOrderInfo from '@/components/header-order-info.vue'
import shortMessage from '@/components/shortMessage.vue'
import Protocal from '@/components/protocal.vue'
let ERR_OK = 'success'; //返回成功码
export default {
  name: 'AddBankCard',
  data () {
    return {
      // isAuthorization: this.isAuthorization ? this.isAuthorization:'', //是否授权成功
      isProtocal:false, //服务协议显示
      isShowMask: false,  //遮罩
      bankCardInfo:{}, //添加得银行卡信息 尾号和类型 [{"bankCode":"ABC","bankName":"ff银行","cardtype":"信用卡","number":"2218"}]
      cardItemNecessary:{},//卡必填项
      cardInfo:{},//卡信息
      smsCode: '',//验证码值
      isShowVerify: false, //短信
      check: true,//同意协议
      contextPath:contextPath, //图片路径
      isAuthorizationPay:'failed',//是否可调预授权确认接口
      smsType:"smsType"//短信类型 返回值  存储
    }
  },
  watch:{
    isAuthorizationPay(){
      var self = this
      this.$bus.$on("isAuthorizationPay",function(msg){
        self.isAuthorizationPay = msg[0]
        if(self.isAuthorizationPay === 'success'){
          // var timer = window.sessionStorage.getItem("timeId");
          // window.clearTimeout(timer)
           self.authorizationPay(msg[1]);  //预授权
        }
      })
    }
  },
  created() {
    var self = this
    this.hideTip() //错误弹框提示
    this.$bus.$on("protocal",function(msg){ //bus监听显示和隐藏协议
      self.isProtocal = msg
      self.isShowMask = msg
    })
    this.$bus.$on("agree",function(msg){   //bus监听同意协议
      self.check = msg
    })
    this.$bus.$on("close",function(msg){   //bus监听同意协议
      self.isShowMask = msg;
      self.isShowVerify = msg;
      
    })
    if(this.$store.state.cardBinInfo){
      this.bankCardInfo = this.$store.state.cardBinInfo
      this.cardItemNecessary = this.$store.state.cardItemNecessary
      this.cardInfo = this.$store.state.cardInfo
    }else{
      this.$http.get(Request.url+'/wap/preauth/ajax/validatecard',{params: self.$route.params}).then((res) => {
        var data =(typeof res.body == 'string') ? JSON.parse(res.body) : res.body 
        if(data.bizStatus == ERR_OK){
          this.bankCardInfo = data.cardBinInfo  //卡bin
          this.cardItemNecessary = data.requiredCardItem  //卡必填项
          this.cardInfo = data.autoFillItem  //卡信息
        }else{//失败
          this.errorProcess(data.errorMsg,data.errorCode)
        }
        
      });
    }
  },
  methods:{ 
    sureAuthorization() {  //确认预授权
      var self = this;
      var result = User.form.autoValidate("#form");
      if (result) {
        var param = User.form.serialize("#form",self.$route.params)
          param.cardType = this.bankCardInfo.cardtype
          param.bankName = this.bankCardInfo.bankName
          param.bankCode = this.bankCardInfo.bankCode
        self.$http.get(Request.url+'/wap/preauth/ajax/firstOrder',{params: param}).then((res) => {
          User.form.enabledSubmit("#submit");
            var data =(typeof res.body == 'string') ? JSON.parse(res.body) : res.body 
            if(data.bizStatus == ERR_OK){
               if(data.smsType){   //需要短验
                  this.$store.state.smsType = data.smsType
                  this.$store.commit("allData")
                  this.showSms()//显示短信
                  this.smsSend()
               }else{
                this.hideSms()//bu显示短信
                this.authorizationPay();
               }
            }else{   //失败
              this.errorProcess(data.errorMsg,data.errorCode)
            }
        });
      }
    },
    authorizationPay(sms){  //预授权确认支付
      var self = this
      var param={}
          param.token = token
          param.verifyCode = sms  //此处是短信验证嘛
      this.$http.get(Request.url+'/wap/preauth/ajax/firstConfirm',{params:param}).then((res) => {
        var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body 
        if(data.bizStatus == ERR_OK){
          window.location.href = this.contextPath+"/wap/query/result?token="+token  //同步查询
        }else{   //失败
          if(data.bizStatus === 'smsFailed'){
             dom.querySelector("#sendText").disabled = true
              this.smsErrorTip(data.errorMsg,data.errorCode)//允许重发短验 
          }else{
              window.location.href = this.contextPath+"/wap/query/result?token="+token  //同步查询
          }
        }
      })
    } 
  },
  components:{
    headerOrderInfo,shortMessage,Protocal
    // Protocal:function(resolve){ require(['@/components/protocal.vue'],resolve)}
  }
}
</script>
<style scoped lang="less">
.mt4{margin-top: 4px;}
.ver-t{ vertical-align: top; }
#submit[disabled]{
  background: #ccc;
}
.content{
  .bankCard{
    height:60px;
    padding-top: 14px;
    .change{
      position: absolute;
      right:12px;
      top:36%;
      width:54px;
      height:18px;
      line-height:18px;
      font-size: 14px;
      color:#2fbf45;
      text-align: right;
      background: url("../../static/pre-authorization-static/assets/images/change-icon.png") no-repeat left ;
      background-size: 20px 16px;
    }
  }
  .verifyBox{
    .errMsg{
      right:88px;
    }
  }
  #form{
    label{
      display: block;
      height:44px;
      mark{
       display: inline-block;
        width: 73px;
        height: 100%;
        line-height: 44px;
        background-color: transparent;
        text-align: justify;
        overflow: hidden;
      }
      mark:after {
          content: " ";
          display: inline-block;
          width: 100%
      }
    }
    .info{
      width:100%;
      height:44px;
      line-height: 44px;
      .text{
        width:72px;
        margin-right: 14px;
      }
      .input-text{
        width:calc(100%-38px);
        height: 44px;
        vertical-align: top;
        &.verifyCode{height: 42px;}
      }
      .resendMessage{
        background: #f0f0f0;
        color:#666;
        height:34px;
        line-height: 34px;
        top:4px;
        right:12px;
      }
      .getCode{
        width:92px;
        height:34px;
        line-height: 34px;
        background: #2eb944;
        color:#fff;
        top:4px;
        right:12px;
        &:active{
          background: #2ea009;
        }
      }
    }
  }
  
  #agreement:checked +.icon.agreement{
    &:before{
      display: inline-block;
      width:18.4px;
      height:18.4px;
      line-height: 18.4px;
      content:"\e7c0";
      font-size:16px;
      color:#2eb944;
      vertical-align: top;
    }
  }
  #agreement +.icon.agreement{
    &:before{
      display: inline-block;
      width:18.4px;
      height:18.4px;
      line-height: 18.4px;
      content:"\e7c1";
      font-size:16px;
      color:#ccc;
      vertical-align: top;
    }
  }
  .mt8{margin-top: 8px;}
  .text{ line-height: 20px; }
}
</style>



