<template>
	<div id="shortMessage">
		<div class="verify bgfff tc " v-show="isShowVerify">
        <p class="tc h40 lh40 c333 fs18 bb pr">请输入验证码<i class="icon pa fs24 close" @click="close">&#xe60f;</i></p>
        <p class="c666  fs18 tc mt10 pl12 pr12" id="smsText">验证码发成功</p>
        <form class="pr" name="form">
          <label for="verifyCode" class="clear bb bl br dis-inline inputBox" id="inputBox" @click="authorizedVerifycode">  <!-- 短信 -->
            <span class="fl span"></span>
            <span class="fl span"></span>
            <span class="fl span"></span>
            <span class="fl span"></span>
            <span class="fl span"></span>
            <span class="fl span"></span>
            <input type="tel" id="verifyCode" maxlength="6" v-model="smsCodeText" />
          </label>
        </form>
        <button class="w button fs18 mt30" type="button"  id="sendText" @click="smsSend">重新获取</button>
      </div>
	</div>
</template>
<script>
export default {
  name: 'shortMessage',
  data () {
     return {
       smsCodeText: ''//验证码值
     }
  },
  props:{
    isShowVerify:false 
  },  
  watch:{
    smsCodeText(){   //输入到第六为掉支付接口
      var reg =  /\d+$/;  
      var smsText = this.smsCodeText
      if(!reg.test(smsText.substring(smsText.length-1))){
        this.smsCodeText = smsText.substring(0,smsText.length-1)
      }
      if(this.smsCodeText.length == 6){
        var sendData = ['success',this.smsCodeText]
        this.$bus.$emit('isAuthorizationPay',sendData) 
      }
    }
  },
  methods:{
    close:function(){  //短信x
      this.$bus.$emit('close',false) 
      var timer = window.sessionStorage.getItem("timeId");
      window.clearTimeout(timer)
    }
  }
}
</script>
<style scoped lang="less">
.resendMessage{background: #f5f5f5;color:#999;}
.verify{   // 验证码
  width:88%;
  position: fixed;
  top:50%;
  left:50%;
  margin-left:-44%;
  margin-top:-40%;
  border-radius:4px;
  overflow: hidden;
  z-index:11;
  .close{
    right:0px;
    padding:0 10px;
    top:0px;
  }
  .button{
    height:44px;
    line-height:44px;
    border-radius:0px;
  }
  .timer{
    background: #f5f5f5;
    color:#999;
  }
  .h40{height:44px;}
  .lh40{line-height:44px;}
  .h30{height:30px;}
  .lh30{line-height:30px;}
  
  .inputBox{
    z-index: 11;
    border-radius:4px;
    overflow: hidden;
    margin-top:8px;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    #verifyCode{
      position: absolute;
      z-index: 11;
      left: -200px;
      top: 0;
      opacity: 0;
      width: 1px;
      height: 1px;
      font-size: 12px;
      -webkit-appearance: none;
    }
    .span{
      width:44px;height:44px;
      line-height: 44px;
      text-align: center;
      border-left:1px solid #ddd;
      border-top:1px solid #ddd;
      position: relative;
      font-size:18px;
      z-index:11;
      &:first-of-type{border-radius:4px 0 0 0;border-left:none;}
    }
  }
}
</style>


