<template>
  <div class="addBankCard">
    <div class="wrapper b-transparent">
      <!-- 头部 start -->
      <headerOrderInfo></headerOrderInfo>
      <!-- 银行卡部分 --> 
      <div class="bankCard icon pl12 bt bb pr" :class='{"bgEBEBE4":!useableBankCards.length,"bgfff":useableBankCards.length}'>   <!-- 点击显示银行弹框 -->
        <div class="dis-inline ver-t mt4">
          <img  :src='this.contextPath+"/static/images/bank-logo/"+bankCardFirst.bankCode+".png"' width="16" height="16" alt="banklogo">
          <!-- <img :src='"../../static/images/bank-logo/"+bankCardFirst.bankCode+".png"' width="16" height="16" alt="banklogo"> -->
        </div>
        <div class="dis-inline">
          <p class="fs14 c666 mb10">{{bankCardFirst.bankName}}</p>
          <i class="fs14 c666 mr10">尾号{{bankCardFirst.cardlater}}</i>
          <i class="fs12 c999 mt10" v-if="bankCardFirst.cardtype == 'DEBIT'">储蓄卡</i>
          <i class="fs12 c999 mt10" v-if="bankCardFirst.cardtype == 'CREDIT'">信用卡</i>
        </div>
        <span class="change" @click="showBankCardTip">更换</span>
      </div>
    </div>
    <div class="pt10 pb10 pl12">
      <p class="fs14 c999">
      <label for="agreement">
        <input class="hide" id="agreement" type="checkbox" :checked="agree" v-model="agree">
        <i class="icon agreement"></i>已阅读并同意
      </label><a class="c2eb944 pr protocal" @click="showProtocal">《服务协议》</a></p>
    </div>
    <div class="pl12 pr12 mt10"> 
      <button class="w" :disabled="isDisable" id="submit" type="button" @click="authorizationOrder">
        <i>确认预授权</i>
      </button>
    </div>
    <!-- 底部 开始 -->
    <foot class="foot"></foot>
    <!-- 底部结束 -->
    <!-- 银行卡列表弹框开始 -->
    <div class="bank-tip" v-show="isShowBankTip">
        <h2>更换银行卡<i class="icon pa fs24 close" @click="closeBBankTip">&#xe60f;</i></h2> <!-- 可用银行和不可用银行合成一个来处理 bankCardInfo -->
        <div class="bank-content">
       
        <!-- 可用列表  start -->
          <div class="bt pt10 pb10" v-for="bank of useableBankCards" @click="getBankInfo(bank)">
            <div class="dis-inline ver-mid pl20"><img :src='this.contextPath+"/static/images/bank-logo/"+bank.bankCode+".png"' width="30" height="30" alt="banklogo"></div>
            <div class="dis-inline ver-mid">
              <p class="fs12 c666">{{bank.bankName}}(尾号{{bank.cardlater}})</p>
              <i class="fs12 c999 mt10" v-if="bank.cardtype == 'DEBIT'">储蓄卡</i>
              <i class="fs12 c999 mt10" v-if="bank.cardtype == 'CREDIT'">信用卡</i>
            </div>
          </div>
          <!-- 可用列表 end -->
          <!-- 不可用列表 start -->
          <div class="bt pt10 pb10 bgf5f5f5" v-for="bank of unuseableBankCards">
            <div class="dis-inline ver-mid pl20"><img :src='this.contextPath+"/static/images/bank-logo/"+bank.bankCode+".png"' width="30" height="30" alt="banklogo"></div>
            <div class="dis-inline ver-mid">
              <p class="fs12 c666">{{bank.bankName}}(尾号{{bank.cardlater}})</p>
              <i class="fs12 c999 mt10" v-if="bank.cardtype == 'DEBIT'">储蓄卡</i>
              <i class="fs12 c999 mt10" v-if="bank.cardtype == 'CREDIT'">信用卡</i>
              <i class="fs12 c999 mt10">{{bank.unusableCause}}</i>
            </div>
          </div>
          <!-- 不可用列表 end -->
        </div>
        <router-link v-if="showChangeCard" tag="div" class="bt add fs16 li" :to="{name: 'EnterCardNo',path:'/enterCardNo'}">
            <div class="dis-inline ver-mid">
              <i class="icon addSymbol fs20 c999">&#xe678;</i>
              <i class="c333 ml6">新增银行卡</i>
            </div>
        </router-link>
    </div>
    <!-- 短信 START-->
    <shortMessage :isShowVerify="isShowVerify"></shortMessage>
    <!-- 短信 END-->
    <!-- 协议 START-->
    <Protocal :isProtocal="isProtocal"></Protocal>
    <!-- 协议 END-->
    <!-- 银行卡列表弹框结束 -->
    <!-- 遮罩层 开始 -->
    <div class="bg-fixed" v-show="isShowMask" id="mask" @click="mask"></div>
    <!-- 遮罩层结束 -->
  </div>
</template>
<script>
import headerOrderInfo from '@/components/header-order-info.vue'
import foot from '@/components/foot.vue'
import AddItem from '@/views/addItem.vue'
let ERR_OK = 'success'; //返回成功码
export default {
  name: 'AddBankCard',
  data () {
    return {
      isProtocal:false, //服务协议显示
      isShowBankTip:false, //银行卡弹框
      isShowMask: false,  //遮罩
      bankCardFirst:{}, //第一个银行卡信息{"bankCode":"ABC","bankName":"ff银行","cardtype":"信用卡","number":"2218"} 
      bankCardInfo:[], //添加得银行卡信息 尾号和类型 [{"bankCode":"ABC","bankName":"ff银行","cardtype":"信用卡","number":"2218"}]
      useableBankCards:[],
      unuseableBankCards:[],
      isShowVerify: false, //短信
      smsCode: '', //验证码值
      agree: true,//同意协议
      contextPath:contextPath, // 路径
      isAuthorizationPay:'failed',//是否可调预授权确认接口
      showChangeCard:showChangeCard //是否展示换卡支付按钮 
      // smsType:"dddsms"//短信类型 返回值  存储
    }
  },
  computed:{  //计算预授权按钮为disabled
    isDisable:function(){
      return !this.agree || !useableBankCards.length
    }
  },
  watch:{
    isAuthorizationPay(){
      var self = this
      this.$bus.$on("isAuthorizationPay",function(msg){
        self.isAuthorizationPay = msg[0]
        if(self.isAuthorizationPay === 'success'){
          // var timer = window.sessionStorage.getItem("timeId");
          // window.clearTimeout(timer)
           self.authorizationPay(msg[1]);  //预授权确认支付
        }
      })
    }
  },
  created() {
    var self = this
    this.hideTip() //错误弹框提示
    this.$bus.$on("protocal",function(msg){ //bus监听显示和隐藏协议
      self.isProtocal = msg
      self.isShowMask = msg
    })
    this.$bus.$on("agree",function(msg){   //bus监听同意协议
      self.agree = msg
    })
    this.$bus.$on("close",function(msg){   //bus关闭短信
      self.isShowMask = msg;
      self.isShowVerify = msg;
      
    })
    //处理可用和不可用银行卡列表
    this.useableBankCards = useableBankCards
    this.unuseableBankCards = unuseableBankCards
    if(useableBankCards.length){
      this.bankCardFirst = useableBankCards[0] 
    }else{
      this.bankCardFirst = unuseableBankCards[0]
    }
  },
  methods:{
    authorizationOrder(){  //预授权确认下单
      var self = this
      var param={}
          param.token = token
          param.bindId = this.bankCardFirst.bindid
      this.$http.get(Request.url+'/wap/preauth/ajax/bindOrder',{params:param}).then((res) => {
        var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body 
        if(data.bizStatus == ERR_OK){ 
           if(data.smsType){
              this.$store.state.smsType = data.smsType  //短信
           } 
           if(data.requiredCardItem){  //业务是：需要补充项跳补充项页面，再判断是否需要短信
              this.$store.state.cardItemNecessary = data.requiredCardItem //需要得补充项
              this.$store.state.cardInfo = data.autoFillItem //卡信息
              this.$router.push({ name: 'AddItem',path:'/AddItem',component: AddItem,params:{'token':token,'bindId':param.bindId}}) // 跳补充项
           }else{   //业务是：不需要补充项，判断是否需要短信
              if(data.smsType){ //只需要短信，不需要补充项
                this.showSms()//显示短信
                this.smsSend()
              }else{//不需要补充项，也不需要短验
                this.authorizationPay()
              }
           }
          this.$store.commit("allData")
        }else{   //失败
          this.errorProcess(data.errorMsg,data.errorCode)
        }
      })
    },
    authorizationPay(sms){ //预授权确认支付  ps:这个页面里的确认支付一定不需要补充项，所以入参不用考虑补充项，只看短信即可
      var self = this
      var param = {}
          param.token = token
          if(sms){  //需要短验，存在值
            param.verifyCode = sms
          }
      this.$http.get(Request.url+'/wap/preauth/ajax/bindConfirm',{params:param}).then((res) => {
        var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body 
        if(data.bizStatus == ERR_OK){  
          window.location.href = this.contextPath+"/wap/query/result?token="+token  //同步查询
        }else{   //失败
          if(data.bizStatus === 'smsFailed'){
              this.smsErrorTip(data.errorMsg,data.errorCode)//允许重发短验  
           }else{
              window.location.href = this.contextPath+"/wap/query/result?token="+token  //同步查询
           }
        }
      })
    },
    getBankInfo(bank){
        this.bankCardFirst = bank
        this.closeBBankTip()
    },
    showBankCardTip(){
      this.isShowMask = true
      this.isShowBankTip = true
    },
    closeBBankTip() {
      this.isShowMask = false
      this.isShowBankTip = false
    }
  },
  components:{
    headerOrderInfo,foot,
    Protocal:function(resolve){ require(['@/components/protocal.vue'],resolve)},
    shortMessage:function(resolve){ require(['@/components/shortMessage.vue'],resolve)}
  }
}
</script>
<style scoped lang="less">
#submit[disabled]{
  background: #ccc;
}
.bankCard{
  height:60px;
  padding-top: 14px;
  .change{
    position: absolute;
    right:12px;
    top:36%;
    width:54px;
    height:18px;
    line-height:18px;
    font-size: 14px;
    color:#2fbf45;
    text-align: right;
    background: url("../../static/pre-authorization-static/assets/images/change-icon.png") no-repeat left ;
    background-size: 20px 16px;
  }
}
#agreement:checked +.icon.agreement{
  &:before{
    display: inline-block;
    width:18.4px;
    height:18.4px;
    line-height: 18.4px;
    content:"\e7c0";
    font-size:16px;
    color:#2eb944;
    vertical-align: top;
  }
}
#agreement +.icon.agreement{
  &:before{
    display: inline-block;
    width:18.4px;
    height:18.4px;
    line-height: 18.4px;
    content:"\e7c1";
    font-size:16px;
    color:#ccc;
    vertical-align: top;
  }
}
.bank-tip{   //银行卡弹框开始
  width:88%;
  background: #fff;
  position: fixed;
  top:40%;
  left:50%;
  margin-left:-44%;
  margin-top:-130px;
  border-radius:4px;
  transition:opacity 0.5s;
  z-index:11;
  h2{
    padding:10px;
    height:20px;
    font-weight: 700;
    font-size: 16px;
  }
  .close{
    height:40px;
    line-height:40px;
    padding:0 10px;
    right:0px; top:0;
  }
  .bank-content{
    max-height: 290px;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
  } 
  .add{
      padding:14px 12px;
      a{
        color:#666;
      }
    }
  .addSymbol{
    width:30px;height:30px;
    text-align: center;
    display: inline-block;
  }
}   //银行卡弹框结束
.foot{width:60%;position: fixed;bottom:30px;left:50%;margin-left:-30%}
</style>



