<template>
  <div class="enterCardNo">
    <!-- 头部 start -->
    <HeaderOrderInfo></HeaderOrderInfo>
    <!-- 头部 end -->
    <p class="ca9a9a9 h42 fs12 pl12 pr12">请使用<i class="cff791f">本人银行卡</i>进行预授权</p>
    <!-- <form class="bgfff" id="form" @submit="noSubmit($event)"> -->
    <form class="bgfff" id="form" @submit.prevent>
      <label class="li pr pl12 pr12 fs18">
        <i class="card-no ">卡号</i>
        <input class="required bankCard input-text w78" name="cardno" _name="cardno" type="tel" placeholder="请输入银行卡号" v-model="cardNo">
        <span class="del pa icon"  @click="delInfo($event)">&#xe65d;</span>
      </label>

    </form>
    <div class="pl12 pr12" >
      <button class="w mt30" id="submit" type="button" @click="toAddBankCard">下一步</button>
    </div>
    <div class="mt10">
      <router-link tag="span" class="c2eb944 fs16 pl12 " to="/SupportBank">可用银行列表</router-link>
      <router-view></router-view>
    </div>
    <div class="bg-fixed" v-show="isShowMask" id="mask"></div>
  </div>
</template>

<script>

let ERR_OK = 'success'; //返回成功码
import AddBankCard from '../views/addBankCard.vue'
import HeaderOrderInfo from '../components/header-order-info.vue'
export default {
  name: 'EnterCardNo',
  data () {
    return {
      selected:'请选择',
      cardNo:'',
      addItem:{},
      isShowMask:false, //mask遮罩
      // token:token,
      companyText:companyText,//公司名称
      amount:amount,//金额
      productName:productName,//产品名称
      orderId:orderId,//订单号
      obj1:{},  //test
      showOrderInfo:false  //展示订单信息
    }
  },
  watch:{
    cardNo(){
      if(this.cardNo.length > 23){
        this.cardNo = this.cardNo.substring(0,23);
      }
    }
  },
  mounted() { 
    this.hideTip() //错误弹框提示
    User.form.validate("#form");
  },
  methods:{
    noSubmit(event){
      event.preventDefault();
      return false;
    },
    toAddBankCard() {
      var self = this
      var result = User.form.autoValidate("#form");
      if (result) {
        User.form.disabledSubmit("#submit");
        this.$http.get(Request.url+'/wap/preauth/ajax/validatecard',{params: {cardNo:BASE64.encoder(this.cardNo),'token':token}}).then((res) => {
          User.form.enabledSubmit("#submit");
            var data =(typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
            if(data.bizStatus == ERR_OK){
              this.$store.state.cardBinInfo = data.cardBinInfo;   //卡bin
              this.$store.state.cardItemNecessary = data.requiredCardItem;  //卡必填项
              this.$store.state.cardInfo = data.autoFillItem; //卡信息
              this.$store.commit("allData");
              this.$router.push({ name: 'AddBankCard',path:'/AddBankCard',component: AddBankCard,params: {cardNo:BASE64.encoder(this.cardNo),'token':token}})
            }else{
              this.errorProcess(data.errorMsg,data.errorCode)
            }
        });
      }
    }
  },
  components:{
    HeaderOrderInfo
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
label{display: block;height:44px;line-height: 32px;}
.h42{line-height: 42px;}
.w78{width:78%;}
.frontFail{
  padding-top: 50px;
  text-align: center;
  .failed{
    color:#ff722d;
    font-size:58px;
  }
}
.bankCard{   //银行卡
  height:32px;
   padding:6px;
   vertical-align: top;
 }
.card-no{   //卡号text
  display: inline-block;
  width:40px;
  height:44px;
  line-height:44px;
  text-align: justify;
  margin-right:10px;
  &:after{
    display: inline-block;
    content:'';
    width:100%;
  }
}
</style>



