<template>
  <div id="headerOrderInfo">
    <div class="company-info bgfff">
        <div class="company-info-box pr">
            <p class="fs14 tc c666 pl12 pr12 pr">
              {{companyText}}
              <i class="icon pa fs26 back" @click="goBack">&#xe610;</i>
            </p>

            <p class="fs30 cff791f tc mt10 ">￥{{amount}}</p>
            <label for="orderInfo" class="icon  orderInfo pa"  :class='{"order-info-y":this.showOrderInfo,"order-info-n":!showOrderInfo}'>
            </label>
        </div>
        <div class="order-details pr " >
            <input class="checkbox" type="checkbox" :checked="showOrderInfo" v-model="showOrderInfo" id="orderInfo">
            <div class="order-info-box  pl12 pr12 c9e9e9e fs16  bt" v-show="showOrderInfo">
              <div class="details-info clear">
                  <div class="fl ">商品名称</div>
                  <div  class="fr information">{{productName}}</div>
              </div>
              <div class="details-info clear">
                  <div class="fl ">订单编号</div>
                  <div class="fr information">{{orderId}}</div>
              </div>
            </div>
          </div> 
      </div>
  </div>
</template>
<script>
export default {
  name: 'headerOrderInfo',
  data () {
    return {
      companyText:companyText,//公司名称
      amount:amount,//金额
      productName:productName,//产品名称
      orderId:orderId,//订单号
      showOrderInfo:false  //展示订单信息
    }
  },
  methods:{
    goBack(){
      // window.history.back(-1)
      this.$router.go(-1)
    }
  }
}
</script>
<style scoped lang="less">

label{display: block;height:44px;}
.company-info{
  .company-info-box{padding:20px 0 20px;}
  .back{
    top:0;left:12px;color:#aaa;
  }
  .orderInfo{
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    right:0px;
    bottom:20px;
  }
  .order-info-y{   //显示订单信息
     width:40px;
     height: 40px;
    &:after{
      width:30px;
      height: 30px;
      position: absolute;
      left:0px;
      bottom: 0px;
      content:"\e608";
      font-size:28px;
      color:#aaa;
    }
  }
  .order-info-n{  //隐藏订单信息
    width:40px;
    height: 40px;
    &:after{
      width:30px;
      height: 30px;
      position: absolute;
      left:0px;
      bottom: 0px;
      content:"\e609";
      font-size:28px;
      color:#aaa;
    }
  }
  .details-info{line-height: 30px;}
}


 
</style>


