// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Vuex from 'vuex'
import App from './App'
import router from './router'
import BASE64 from '../static/pre-authorization-static/js/jbase64.min.js'
import User from '../static/pre-authorization-static/js/user.js'
import Util from '../static/pre-authorization-static/js/util.js'
import Tip from '../static/pre-authorization-static/js/tip.js'
import Request from '../static/pre-authorization-static/js/request.js'
import Common from '../src/util/util.js'
import'../static/pre-authorization-static/css/h5.css'
import'../static/pre-authorization-static/css/tip.less'

Vue.use(Common)
Vue.use(Vuex)
Vue.config.productionTip = false
const vuex_store = new Vuex.Store({
	state:{
		cardBinInfo:null, //卡宾信息
		cardItemNecessary:null,
		cardInfo:null,
		smsType:''
	},
	mutations:{
		allData(state){
			state.cardBinInfo
			state.cardItemNecessary
			state.cardInfo
			state.smsType
		}
	}
});
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store:vuex_store,  //vuex对象挂载到vue对象上
  template: '<App/>',
  components: { App }
})



