export default{
	install:function(Vue,opt){
		Vue.mixin({
			methods:{
				showProtocal() {
			      // 服务协议 
			      this.isShowMask = true;
			      this.isProtocal = true;
			    },
			    showSms(){ //短信显示
					this.isShowMask = true;
					this.isShowVerify = true;
			    },
			    hideSms(){ //短信显示
			    	this.isShowMask = false;
					this.isShowVerify = false;
			    },
			    mask:function(){ //遮罩点击
			    	this.isProtocal = false; // 服务协议 
			    	this.isShowMask = false;
			    	this.isShowVerify = false;
			    	this.isShowBankTip = false;
			    	 
			    	var timer = window.sessionStorage.getItem("timeId");
          			if(timer){
          				window.clearTimeout(timer)
          			}
			    	var tip1 = dom.querySelector("#tip1")
			    	if(tip1){
			    		tip1.classList.add("none");
			    	}
			    },
			     hideTip(){  //错误弹框提示
					var tip1 = dom.querySelector("#tip1")
			    	if(tip1){
			    		tip1.classList.add("none");
			    	}
			    },
			    authorizedVerifycode:function() {   ////短信
			    	this.smsCodeText = '';
				    var spans = document.querySelectorAll("#inputBox .span");//验证码每一个单元格
				    var verifyCodeField = dom.querySelector("#verifyCode");
				    verifyCodeField.addEventListener("input", function () {
			        var reg =  /\d+$/;
			        var len = dom.querySelector("#verifyCode").value.length-1
			        var val = dom.querySelector("#verifyCode").value
			          if(!reg.test(val.charAt(len))){
			      		dom.querySelector("#verifyCode").value = val.substring(0,len)
			          }
			          for (var i = 0, l = 6; i < l; i++) {
			              if(typeof this.value[i] != "undefined"){
			                spans[i].innerHTML = this.value[i]
			              }else{
			                spans[i].innerHTML = "";
			              }
			          }
			      }, false);
			    },
			    smsErrorTip(errorMsg,errorCode){  //允许重发短验  短信输入错误 提示
			    	this.isShowMask = true
		            this.isShowVerify = true
		            dom.querySelector("#smsText").innerHTML = errorMsg+"("+errorCode+")" 
		            dom.querySelector("#sendText").disabled = false
			    },
			    clearSmsContent(){ //清空短信内容
			    	dom.querySelector("#verifyCode").value = ''
		            Array.from(dom.querySelectorAll("#inputBox .span")).forEach(function(item,index){
		              return item.innerHTML = '';
		            })
			    },
			    smsSend() {   //发送短验接口
			      var param={};
			      if(dom.querySelector("#form")){
 					param = User.form.serialize("#form")  //加密的补充项
			      }
		          	param.token = token 
		          	param.smsType = this.$store.state.smsType   //刷新注意
			      this.clearSmsContent() //清空短信内容
			      this.$http.get(Request.url+'/wap/preauth/ajax/smsSend',{params:param}).then((res) => {
			        var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body 
			        var target = dom.querySelector("#sendText")
			        User.autoValidateCode(target);//获取验证码开始倒计时
			        if(data.bizStatus === 'success'){ //success
			          dom.querySelector("#smsText").innerHTML = '短信发送成功'
			          this.isAuthorizationPay = 'success'
			        }else{ //fail
			           this.isAuthorizationPay = 'failed';
			            if(data.bizStatus === 'smsFailed'){
			                this.smsErrorTip(data.errorMsg,data.errorCode)//允许重发短验   
			            }else{
			              	window.location.href = contextPath+"/wap/fail/?token="+token+"&errorCode="+data.errorCode+"&errorMsg="+data.errorMsg+"&needRepay="+true+"&payTool=YSQ";
			            }
			        }
			      })
			    },
			    formatCheck(item) {  //
		    		var form = dom.querySelector('#form');
		    		User.form.hideErrorMessage(form);  //得到焦点隐藏错误信息
			    	var result = User.form.doValidate(item.srcElement);
                    if (!result) {
                        return false;
                    }
			    },
			    errorProcess(msg,code){ //请求失败，错误提示，统一处理
			    	var self = this
			    	this.isShowMask = true
			        Tip.titleContentBtn('提示',msg+'('+code+')',function(){
			            dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
			              self.isShowMask = false;
			              dom.querySelector("#tip1").classList.add("none")
			            },false);
			        })
			    }
			}
		});
		Vue.prototype.delInfo=function(e){  //删除输入框内容
		  	var obj = e.target;
		  	obj.parentNode.querySelector(".input-text").value=''; 
		}
	}	
}



