<template>
	<div id="addItem">
		 <form class="bt bgfff fs18" name="form" id="form">
          <label v-if="cardItemNecessary.needOwner">
            <div class="info li pr " :class='{"bgEBEBE4":cardInfo.owner}'>
              <mark class="dis-inline text c333 pl12">姓名</mark>
              <input v-if="cardInfo.owner" class=" input-text" name="owner" _name="chinese" encrypt="true" type="text" :value="cardInfo.owner" :disabled="true">
              <input v-else class="required input-text" name="owner" _name="chinese" encrypt="true" type="text" placeholder="您的真实姓名" @blur.prevent="formatCheck($event)">
              <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
            </div>
          </label>          
          <label v-if="cardItemNecessary.needIdNo">
            <div class="info bt li pr error" :class='{"bgEBEBE4":cardInfo.idNo}'>
                <mark class="dis-inline text c333 pl12">身份证号码</mark>
                <input v-if="cardInfo.idNo" class=" input-text" name="idNo" encrypt="true" type="tel" :value="cardInfo.idNo" :disabled="true">
                <input v-else class="required input-text" name="idNo" _name="idno" maxlength="18" encrypt="true" type="tel" placeholder="真实证件号" @blur.prevent="formatCheck($event)">
                <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
            </div>
          </label>
          <div>
            <label v-if="cardItemNecessary.needCvv">
              <div class="info li bt pr" :class='{"bgEBEBE4":cardInfo.cvv}'>
                <mark class="dis-inline text c333 pl12">C V V</mark>
                <input v-if="cardInfo.cvv" class=" input-text" name="cvv"  encrypt="true" type="tel" :value="cardInfo.cvv" :disabled="true">
                <input v-else class="required input-text" name="cvv" _name="threeOrFour" maxlength="4" encrypt="true" type="tel" placeholder="卡背面后三位数字" @blur.prevent="formatCheck($event)">
                  <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
              </div>
            </label>
            <label v-if="cardItemNecessary.needAvlidDate">
              <div class="info bt li pr" :class='{"bgEBEBE4":cardInfo.avlidDate}'>
                  <mark class="dis-inline text c333 pl12">有效期</mark>
                  <input v-if="cardInfo.avlidDate" class=" input-text" name="avlidDate"  encrypt="true" type="tel" :value="cardInfo.avlidDate" :disabled="true">
                  <input v-else class="required input-text" name="avlidDate" _name="fourNo" maxlength="4" encrypt="true" type="tel" placeholder="月/年(如09/15 输入0915)" @blur.prevent="formatCheck($event)">
                  <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
              </div>
            </label>
          </div>
          <label v-if="cardItemNecessary.needPhoneNo" >
            <div class="info bt li pr error" :class='{"bgEBEBE4":cardInfo.phoneNo}'>
              <mark class="dis-inline text c333 pl12">手机号</mark>
              <input v-if="cardInfo.phoneNo" class=" input-text" name="phoneNo" encrypt="true" type="tel" :value="cardInfo.phoneNo" :disabled="true">
              <input v-else class="required input-text" name="phoneNo" _name="phone" maxlength="11" encrypt="true" type="tel" placeholder="银行预留手机号"  @blur.prevent="formatCheck($event)">
              <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
            </div>
          </label>
        </form>
        <!-- 确认预授权 按钮 开始-->
        <div class="pl12 pr12 mt30">
          <button class="w"  id="submit" type="button" @click="authorizationPay">
            <i>确认预授权</i>
          </button>
        </div>
        <!-- 确认预授权 按钮 结束-->
    <!-- 底部 开始 -->
    <foot class="foot"></foot>
    <!-- 底部结束 -->
    <!-- 短信 START-->
    <shortMessage :isShowVerify="isShowVerify"></shortMessage>
    <!-- 短信 END-->
     <!-- 遮罩层 开始 -->
    <div class="bg-fixed" v-show="isShowMask" id="mask" @click="mask"></div>
    <!-- 遮罩层结束 -->
	</div>
</template>
<script>
import foot from '@/components/foot.vue'
import shortMessage from '@/components/shortMessage.vue'
let ERR_OK = 'success'; //返回成功码
export default {
  name: 'AddItem',
  data () {
     return {
      isShowMask: false,  //遮罩
      isShowVerify:false, //短信
      smsCode: '',//验证码值
     	cardItemNecessary:{},//卡必填项
      cardInfo:{},//卡信息
      isAuthorizationPay:'failed',//是否可调预授权确认接口
      smsType:"",//短信类型 返回值  存储
      contextPath:contextPath //路径
     }
  },
  watch:{
    isAuthorizationPay(){
      var self = this
      this.$bus.$on("isAuthorizationPay",function(msg){
        self.isAuthorizationPay = msg[0]
        if(self.isAuthorizationPay === 'success'){  //需要短信情况下，isAuthorizationPay 为success时才可以预授权
          // var timer = window.sessionStorage.getItem("timeId");
          // window.clearTimeout(timer)
           self.authorizationUnneedSms(msg[1]);  //预授权确认支付
        }
      })
    }
  },
  created() {
    var self = this
    this.hideTip() //错误弹框提示
    this.$bus.$on("close",function(msg){   //bus关闭短信
      self.isShowMask = msg;
      self.isShowVerify = msg;
      
    })
    if(this.$store.state.cardItemNecessary){
      this.cardItemNecessary = this.$store.state.cardItemNecessary
      this.cardInfo = this.$store.state.cardInfo
      this.smsType = this.$store.state.smsType
    }else{  
      this.$http.get(Request.url+'/wap/preauth/ajax/bindOrder',{params: self.$route.params}).then((res) => {
        var data =(typeof res.body == 'string') ? JSON.parse(res.body) : res.body 
        if(data.bizStatus == ERR_OK){
          this.cardItemNecessary = data.requiredCardItem  
          this.cardInfo = data.autoFillItem 
          if(data.smsType){
              this.$store.state.smsType = data.smsType  //短信
           }
          if(data.cardItemNecessary){
            this.$store.state.cardItemNecessary = data.requiredCardItem //需要得补充项
            this.$store.state.cardInfo = data.autoFillItem //卡信息
          }
          this.$store.commit("allData");
        }else{  //失败
          this.errorProcess(data.errorMsg,data.errorCode)
        }
        
      });
    }
  },
  methods:{
    authorizationUnneedSms(item){ //预授权确认支付
    var self = this
    var result = User.form.autoValidate("#form") //页面元素格式正确才可以发送数据请求
      if (result) {
        var param = User.form.serialize("#form")
            param.token=token
            if(item){  
              param.verifyCode = item 
            }
        this.$http.get(Request.url+'/wap/preauth/ajax/bindConfirm',{params:param}).then((res) => {
          var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body 
          if(data.bizStatus == ERR_OK){ 
              window.location.href = this.contextPath+"/wap/query/result?token="+token  //同步查询
          }else{   //失败
            if(data.bizStatus === 'smsFailed'){
              this.smsErrorTip(data.errorMsg,data.errorCode)//允许重发短验
            }else{
              window.location.href = this.contextPath+"/wap/query/result?token="+token  //同步查询
            }
          }
        })
      }    
    },
    authorizationPay(){
      if(this.$store.state.smsType){   //Need短验
        var result = User.form.autoValidate("#form");  //页面元素格式正确才可以弹出短信框
        if (result) {
          this.showSms()//显示短信
          this.smsSend()
        }
      }else{ //不需要短验 确认支付
        this.authorizationUnneedSms()
      }
    }
  },
  components:{
    foot,
    shortMessage:function(resolve){ require(['@/components/shortMessage.vue'],resolve)}
  }
}
</script>
<style scoped lang="less">
.bgEBEBE4{background-color:#EBEBE4;}
.foot{width:60%;position: fixed;bottom:30px;left:50%;margin-left:-30%}
 #form{
    label{
      display: block;
      height:44px;
      mark{
       display: inline-block;
        width: 73px;
        height: 100%;
        line-height: 44px;
        background-color: transparent;
        text-align: justify;
        overflow: hidden;
      }
      mark:after {
          content: " ";
          display: inline-block;
          width: 100%
      }
    }
    .info{
    width:100%;
    height:44px;
    line-height: 44px;
    .text{
      width:72px;
      margin-right: 14px;
    }
    .input-text{
      width:calc(100%-38px);
      height: 40px;
      vertical-align: top;
      &.verifyCode{height: 42px;}
    }
    .resendMessage{
      background: #f0f0f0;
      color:#666;
      height:34px;
      line-height: 34px;
      top:4px;
      right:12px;
    }
    .getCode{
      width:92px;
      height:34px;
      line-height: 34px;
      background: #2eb944;
      color:#fff;
      top:4px;
      right:12px;
      &:active{
        background: #2ea009;
      }
    }
  }
  }
</style>


